.tableContainer {
    position: relative;
    max-height:  300px;
    width: 100%;
    overflow: scroll;
    
  }

thead th {
    position: -webkit-sticky;
    position: sticky;
    background: #0c4594;
    color: white;
    text-align: center;
    height: 3rem;
}

  td, th {
    border:  1px solid #0c4594;;
    box-sizing: border-box;
    vertical-align: middle;
    padding: 0px 10px;
}

td {
    text-align: center; 
    vertical-align: middle;
}

table input,
table textarea{
    width: 100%;
    border: none !important;
    background-color: transparent;
    text-align: center;
    resize: vertical;
    font-family: Poppins;
}

table textarea:focus, input:focus{
    outline: none;
}

.addBtn{
    width: fit-content;
    font-size: 15px;
    font-weight: 700;
    padding: 5px;
    color:#0c4594;
    border: none;
    background-color: transparent;
}

.addBtn:hover{
    color: #166ce4ae;
    cursor: pointer;
}

.rmvRow{
    width: fit-content;
    font-size: 15px;
    font-weight: 700;
    padding: 5px;
    color:#ed0f0f;
    border: none;
    background-color: transparent;
}

.rmvRow:hover{
    cursor: pointer;
    color:#ff0404aa;

}

table ::placeholder{
    text-align: center;
}
.tableContainer, textarea{
    &::-webkit-scrollbar {
     width: 0.2em;
     height: 0.2em;
    }
  
    &::-webkit-scrollbar-thumb {
     background-color: #0c4594;
     border-radius: 3px;
    }
  }

  