.headFormContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family:Poppins;
    margin-bottom: 5rem;
}

.Heading{
    display: flex;
    font-size: 1.5rem;
    font-weight: 600;
    color: #0c4594;
    text-shadow: 0px 0px 5px #8d8a8a,
      0px 0px 20px #b393d3;
      align-items: center;
}

.FormContainer{
    width: 80%;
    margin: 0 auto;
    background: #f9f9f9;
    padding: 3rem;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.row{
    display: flex;
    margin-bottom: 15px;
}

.errorMsg{
    color: red !important;
    font-size: 14px;
}

.row div{
    display: flex;
    flex-direction: column;
}

.row label{
    font-weight: 600;
    margin-bottom: 5px;
}

.row div {
    flex: 1;
    margin-right: 10px;
  }
  
.row div:last-child {
  margin-right: 0;
}

.r1, .r4, .r7, .r5, .r6, .r8,
 .r9,.r10,.r11, .r12, .r13, .r14{
    display: flex;
    flex-direction: column;
}

.r1Div, .r5Div,.r2{
    display: flex;
    flex-direction: row !important;
}

.FormContainer input{
    padding: 7px 10px;
    font-family: Poppins;
    font-size: 15px;
}

input:focus{
    border-color: rgb(20, 20, 20);
}
.textNotTable{
    padding: 7px 10px;
    font-family: Poppins;
    font-size: 15px;
}

.essayText{
    height: 80px;
}

.submitBtn{
    text-align: center;
}

.submitBtn button{
    border-radius: 10px;
    border: none;
    width: 100px;
    padding: 10px;
    font-size: 16px;
    font-weight: bold;
    color: white;
    background-color: #0c4594;
}

.GEFLogo{
    height: 100px;
    width: 100px;
    margin-right: 20px;
}

@media screen and (max-width: 675px) {
    .r1Div, .r2, .r5Div{
        display: flex;
        flex-direction: column !important;
    }
}